.odc-m-drag-active {
  cursor: no-drop;
}

.odc-m-drag-active.odc-m-valid-drop-target {
  cursor: grab;
}

.odc-m-drag-active.odc-m-valid-drop-target .odc-m-drag-create {
  cursor: none;
}

.topology-connector__remove-bg {
  fill: #73bcf7;
  cursor: pointer;
}

.odc-m-drag-active .topology-connector__remove-bg {
  display: none;
}

.topology-connector__remove-icon {
  color: var(--pf-v5-global--Color--light-100);
  cursor: pointer;
}

.odc-m-drag-active .topology-connector__remove-icon {
  display: none;
}

