.odc-base-edge {
  --edge--stroke-width: 2px;
  --edge--stroke-dasharray: 0;
  --edge--stroke: #bbbbbb;
  --edge--fill: var(--edge--stroke);
  --edge--opacity: 1;
  --edge--cursor: pointer;
  --edge--active--stroke: #06c;
  --edge--active--fill: var(--edge--active--stroke);
  --edge--drag-active--opacity: 0.4;
  --edge__arrow--cursor: default;
  --edge--interactive--stroke: #73bcf7;
  --edge--interactive--fill: var(--edge--interactive--stroke);
  cursor: var(--edge--cursor);
  stroke: var(--edge--stroke);
  fill: var(--edge--fill);
  opacity: var(--edge--opacity);
}

.odc-base-edge__link {
  stroke-width: var(--edge--stroke-width);
  stroke-dasharray: var(--edge--stroke-dasharray);
}

.odc-base-edge.pf-m-selected {
  fill: var(--edge--active--fill);
  stroke: var(--edge--active--stroke);
}

.odc-base-edge.pf-m-selected .odc-base-edge__link,
.odc-base-edge.pf-m-selected .pf-topology-connector-arrow {
  stroke: var(--edge--active--stroke);
}

.odc-base-edge.is-hover,
.odc-base-edge.pf-m-dragging {
  fill: var(--edge--interactive--fill);
  stroke: var(--edge--interactive--stroke);
}

.odc-base-edge.is-hover .odc-base-edge__link,
.odc-base-edge.is-hover .pf-topology-connector-arrow,
.odc-base-edge.pf-m-dragging .odc-base-edge__link,
.odc-base-edge.pf-m-dragging .pf-topology-connector-arrow {
  stroke: var(--edge--interactive--stroke);
}

.odc-base-edge .pf-topology-connector-arrow {
  cursor: var(--edge__arrow--cursor);
  stroke: var(--edge--stroke);
}

.odc-m-drag-active .odc-base-edge {
  pointer-events: none;
  opacity: var(--edge--drag-active--opacity);
}

.odc-m-drag-active .odc-base-edge.pf-m-dragging {
  opacity: var(--edge--opacity);
}

.odc-m-drag-active .odc-base-edge .pf-topology-connector-arrow {
  pointer-events: none;
}

.odc-m-filter-active .odc-base-edge {
  opacity: var(--edge--drag-active--opacity);
}

.odc-m-filter-active .odc-base-edge.pf-m-dragging {
  opacity: var(--edge--opacity);
}

