.odc-operator-backed-service {
  outline: none;
  cursor: pointer;
}

.odc-m-drag-active .odc-operator-backed-service {
  pointer-events: none;
}

.odc-operator-backed-service__bg {
  fill: #d2d2d2;
  fill-opacity: .5;
  stroke: #8a8d90;
  stroke-width: 2.5px;
  stroke-dasharray: 5 6;
}

.odc-operator-backed-service.is-filtered .odc-operator-backed-service__bg {
  stroke: #ef9234;
  stroke-width: 3px;
  stroke-dasharray: none;
}

.odc-operator-backed-service.pf-m-selected .odc-operator-backed-service__bg {
  fill: #a5c4e2;
  stroke: #06c;
  stroke-width: 3px;
  stroke-dasharray: 5 6;
}

.odc-operator-backed-service.pf-m-highlight .odc-operator-backed-service__bg {
  fill: #f5f5f5;
  stroke: #73bcf7;
}

.odc-operator-backed-service.pf-m-drop-target .odc-operator-backed-service__bg {
  fill: #a5c4e2;
  stroke: #73bcf7;
}

.odc-m-drag-active .odc-operator-backed-service,
.odc-m-filter-active .odc-operator-backed-service {
  opacity: .4;
}

.odc-m-drag-active .odc-operator-backed-service.pf-m-highlight,
.odc-m-drag-active .odc-operator-backed-service.pf-m-dragging,
.odc-m-filter-active .odc-operator-backed-service.pf-m-highlight,
.odc-m-filter-active .odc-operator-backed-service.pf-m-dragging {
  opacity: 1;
}

.odc-m-filter-active:not(.odc-m-drag-active) .odc-operator-backed-service.is-filtered {
  opacity: 1;
}

