.odc-resource-kinds-info {
  padding: var(--pf-v5-global--spacer--md) var(--pf-v5-global--spacer--md) var(--pf-v5-global--spacer--md) 0;
  width: 100%;
  height: 100%;
  overflow-y: auto;
}

.odc-resource-kinds-info__table {
  width: 100%;
}

.odc-resource-kinds-info__table td {
  padding-bottom: var(--pf-v5-global--spacer--sm);
  padding-right: var(--pf-v5-global--spacer--sm);
}

.odc-resource-kinds-info__table td:last-of-type {
  padding-right: 0;
}

.odc-resource-kinds-info__resource-icon {
  width: 1%;
}

.odc-resource-kinds-info__resource-icon .co-m-resource-icon {
  font-size: 80%;
  margin-right: 0;
}

.odc-resource-kinds-info__count {
  text-align: end;
  width: 1%;
}

