.odc-group-node__title {
  font-size: var(--pf-v5-global--FontSize--lg);
  font-weight: var(--pf-v5-global--FontWeight--bold);
}

.odc-group-node__type-icon {
  fill: var(--pf-v5-global--Color--light-100);
  font-size: var(--pf-v5-global--FontSize--md);
}

.odc-group-node__bg {
  fill: #d2d2d2;
  fill-opacity: .5;
  stroke: #8a8d90;
  stroke-width: 2.5px;
  stroke-dasharray: 5 6;
}

.odc-group-node.is-filtered .odc-group-node__bg {
  stroke: #ef9234;
  stroke-width: 3px;
  stroke-dasharray: none;
}

.odc-group-node.pf-m-selected .odc-group-node__bg {
  fill: #a5c4e2;
  stroke: #06c;
  stroke-width: 3px;
  stroke-dasharray: 5 6;
}

.odc-group-node.pf-m-highlight .odc-group-node__bg {
  fill: #d2d2d2;
  stroke: #73bcf7;
  stroke-dasharray: 5 6;
}

.odc-group-node.pf-m-drop-target .odc-group-node__bg {
  fill: #a5c4e2;
  stroke: #73bcf7;
  stroke-dasharray: 5 6;
}

.odc-m-drag-active .odc-group-node,
.odc-m-filter-active .odc-group-node {
  opacity: .4;
}

.odc-m-drag-active .odc-group-node.pf-m-dragging,
.odc-m-drag-active .odc-group-node.pf-m-highlight,
.odc-m-filter-active .odc-group-node.pf-m-dragging,
.odc-m-filter-active .odc-group-node.pf-m-highlight {
  opacity: 1;
}

