.odc-application-group {
  cursor: pointer;
}

.odc-m-drag-active .odc-application-group {
  pointer-events: none;
}

.odc-application-group .pf-topology__group__background {
  fill: #d2d2d2;
  fill-opacity: .5;
}

.odc-application-group.is-filtered .pf-topology__group__background {
  stroke: #ef9234;
}

.odc-application-group.pf-m-highlight .pf-topology__group__background {
  fill: #f5f5f5;
  stroke: #73bcf7;
}

.odc-application-group.pf-m-selected .pf-topology__group__background {
  stroke: #06c;
  fill: #a5c4e2;
}

.odc-m-drag-active .odc-application-group,
.odc-m-filter-active .odc-application-group {
  opacity: .4;
}

.odc-m-drag-active .odc-application-group.pf-m-highlight,
.odc-m-drag-active .odc-application-group.pf-m-dragging,
.odc-m-filter-active .odc-application-group.pf-m-highlight,
.odc-m-filter-active .odc-application-group.pf-m-dragging {
  opacity: 1;
}

.odc-m-filter-active:not(.odc-m-drag-active) .odc-application-group.is-filtered {
  opacity: 1;
}

