:root {
  --pf-v5-global--FontSize--md: 14px;
  --pf-global--FontSize--md: 14px;
  --pf-v5-global--FontSize--sm: 13px;
  --pf-global--FontSize--sm: 13px;
}

.odc-resource-icon {
  font-size: 80%;
  fill: #2b9af3;
}

.odc-resource-icon text {
  fill: var(--pf-v5-global--palette--white) !important;
}

.odc-resource-icon-service {
  fill: #6ca100;
}

.odc-resource-icon-deployment,
.odc-resource-icon-daemonset,
.odc-resource-icon-deploymentconfig,
.odc-resource-icon-job {
  fill: #004080;
}

.odc-resource-icon-application {
  fill: #3e8635;
}

.odc-resource-icon-pod {
  fill: #009596;
}

